@import "../Assets/scss/variables";

.products {
  width: calc(100% - 18rem);
  position: fixed;
  top: 4rem;
  left: 14rem;
  padding: 2rem;
  color: $dark_color;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 1.4rem;
  font-weight: 600;
  h3 {
    margin: 0 0 1rem 0;
  }
  .products_table {
    width: 100%;
    height: 80vh;
    overflow: scroll;
  }
  .products_table::-webkit-scrollbar {
    width: 0 !important;
  }
  antd-table-custom-class thead th,
  .antd-table-custom-class tbody td {
    white-space: nowrap;
    word-break: break-word;
    word-break: break-all;
  }
}
